import { useLoaderData } from 'remix';
import blogStyles from '~/styles/blog.css';
import client from '~/contentfulConfig'; // Adjust the path to your contentfulConfig file

export async function loader({ params }) {
  const response = await client.getEntry(params.PostId);
  if (!response) throw new Error('No posts found');

  const post = {
    id: response.sys.id,
    title: response.fields.title,
    content: response.fields.content,
  };

  return post;
}

export default function GetPost() {
  const post = useLoaderData();

  return (
    <>
      <h2>{post.title}</h2>
      <p>{post.content}</p>
    </>
  );
}
